.about-us {
  background:
    url("../img/svg/about.svg") 0 0 / 28.5% no-repeat,
    linear-gradient($color-east-bay);

  @include mobile {
    padding: 25px 0;
  }
}

.about-us__inner {
  display: flex;
  justify-content: space-between;
  align-items: stretch;

  @include mobile {
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
  }
}

.about-us__picture {
  clip-path: polygon(26.5% 0, 100% 0, 73.5% 100%, 0 100%);

  @include tablet {
    width: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  @include mobile {
    width: 100%;
  }
}

.about-us__promo {
  width: 38%;
  padding: 218px 0 0 5px;

  @include tablet {
    min-width: auto;
    padding: sz-t(15px) 0 0 0;
  }

  @include mobile {
    width: 100%;
    margin-bottom: 20px;
  }
}

.about-us__title {
  margin: 0 0 21px 0;
}

.about-us__quote {
  width: 89%;
  margin: 0 0 20px 0;
  font-family: $ff-default;
  font-size: 16px;
  line-height: 22px;
  color: $color-default-white;
}

.about-us__quote-author {
  position: relative;
  font-family: $ff-intro;
  font-weight: 700;
  font-size: 20px;
  line-height: 20px;
  color: $color-default-white;
  display: block;
  text-align: right;

  &::before {
    position: absolute;
    top: 50%;
    right: 27%;
    transform: translateY(-50%);
    content: "";
    width: sz(28px);
    height: sz(1px);
    border: sz(1px) solid $color-default-white;

    @include mobile {
      right: 100px;
    }
  }
}
